"use client";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import Loader from "@/lib/Loader";
import { logout } from "@/service/auth.service";
import { getAllUsers } from "@/service/user.service";
import useSidebarStore from "@/store/sidebarStore";
import userStore from "@/store/userStore";
import {
  Bell,
  Home,
  LogOut,
  Menu,
  MessageCircle,
  Moon,
  Search,
  Sun,
  Users,
  Video,
} from "lucide-react";
import { useTheme } from "next-themes";
import Image from "next/image";
import { useRouter } from "next/navigation";
import React, { useEffect, useRef, useState } from "react";
import toast from "react-hot-toast";

<style jsx>{` .custom-shadow { box-shadow: 0 4px 8px rgba(0, 0, 0, 0.9); /* Adjust the RGBA values to make the shadow darker */ } `}</style>

const Header = () => {
  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const [searchQuery,setSearchQuery] = useState("");
  const [userList,setUserList] = useState([])
  const [filterUsers,setFilterUsers] = useState([])
  const [loading,setLoading] = useState(false);
  const [activeTab,setActiveTab] = useState("home");
  const searchRef = useRef(null)
  const { theme, setTheme } = useTheme();
  const { toggleSidebar } = useSidebarStore();
  const router = useRouter();
  const { user, clearUser } = userStore();

  const userPlaceholder = user?.username
    ?.split(" ")
    .map((name) => name[0])
    .join("");

  const handleNavigation = (path, item) => {
    router.push(path);
    setActiveTab(item)
  };

  const handleLogout = async () => {
    try {
      const result = await logout();
      if (result?.status == "success") {
        router.push("/user-login");
        clearUser();
      }
      toast.success("user logged out successfully");
    } catch (error) {
      console.log(error);
      toast.error("failed to log out");
    }
  };


  useEffect(() =>{
    const fetchUsers = async () => {
       try {
         setLoading(true);
         const result = await getAllUsers()
         setUserList(result);
       } catch (error) {
         console.log(error);
       }finally{
        setLoading(false);
       }
    }
    fetchUsers();
  },[])

  useEffect(() =>{
    if(searchQuery){
      const filterUser = userList.filter(user => {
       return  user.username.toLowerCase().includes(searchQuery.toLowerCase())
      })
      setFilterUsers(filterUser);
      setIsSearchOpen(true)
    }else{
      setFilterUsers([])
      setIsSearchOpen(false)
    }
  },[searchQuery,userList])

  const handleSearchSubmit = (e) =>{
     e.preventDefault()
     setIsSearchOpen(false)
  }
  
  const handleUserClick = async(userId) =>{
     try {
       setLoading(true)
       setIsSearchOpen(false)
       setSearchQuery("")
       await router.push(`user-profile/${userId}`)
     } catch (error) {
       console.log(error)
     }finally{
      setLoading(false)
     }
  }

  const handleSearchClose = (e) =>{
        if(!searchRef.current?.contains(e.target)){
          setIsSearchOpen(false)
        }
  }
   useEffect(() =>{
    document.addEventListener("click",handleSearchClose)
    return () => {
      document.removeEventListener("click",handleSearchClose)
    }
   })


   if(loading){
    return <Loader/>
   }


  return (
    <header className="bg-background-header dark:bg-[rgb(2,1,10)] text-foreground shadow-lg h-16 fixed top-0 left-0 right-0 z-50 p-2 custom-shadow">
  <div className="mx-auto flex justify-between items-center p-2">
    <div className="flex items-center gap-2 md:gap-4">
      <Image
        src="/images/SphereBreak_logo1_2.png"
        width={40}
        height={40}
        alt="app_logo"
        onClick={() => handleNavigation("/")}
        className="cursor-pointer rounded-full"
      />
      <div className="relative" ref={searchRef}>
        <form onSubmit={handleSearchSubmit}>
          <div className="relative">
            <Search className="absolute left-2 top-1/2 transform -translate-y-1/2 text-foreground" />
            <Input
              className="pl-8 w-40 md:w-64 h-10 bg-[rgb(230,240,245)] dark:bg-[rgb(10,5,25)] rounded-full text-foreground"
              placeholder="Search People..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onFocus={() => setIsSearchOpen(true)}
            />
          </div>
          {isSearchOpen && (
            <div className="absolute top-full left-0 w-full bg-[rgb(235,245,250)] dark:bg-[rgb(8,4,20)] border border-foreground rounded-md shadow-lg mt-1 z-50">
              <div className="p-2">
                {filterUsers.length > 0 ? (
                  filterUsers.map((user) => (
                    <div
                      className="flex items-center space-x-8 p-2 hover:bg-[rgb(230,240,245)] dark:hover:bg-[rgb(12,6,30)] rounded-md cursor-pointer"
                      key={user._id}
                      onClick={() => handleUserClick(user?._id)}
                    >
                      <div className="flex items-center gap-2">
                        <Avatar className="h-8 w-8">
                          {user?.profilePicture ? (
                            <AvatarImage
                              src={user?.profilePicture}
                              alt={user?.username}
                            />
                          ) : (
                            <AvatarFallback className="text-foreground">
                              {userPlaceholder}
                            </AvatarFallback>
                          )}
                        </Avatar>
                        <span className="text-foreground">{user?.username}</span>
                      </div>
                    </div>
                  ))
                ) : (
                  <div className="p-2 text-foreground">No user Found</div>
                )}
              </div>
            </div>
          )}
        </form>
      </div>
    </div>
    <nav className="hidden md:flex justify-around w-[40%] max-w-md gap-7">
      {[
        { icon: Home, path: "/", name: "" },
        { icon: Users, path: "/friends-list", name: "" },
      ].map(({ icon: Icon, path, name }) => (
        <Button
          key={name}
          variant="ghost"
          size="icon"
          className={`relative text-foreground hover:text-foreground hover:bg-transparent ${
            activeTab === name ? "text-foreground" : ""
          }`}
          onClick={() => handleNavigation(path, name)}
        >
          <div className="flex flex-col items-center text-center">
        <Icon className="w-4 h-4" style={{ width: "20px", height: "20px" }}/>
        <span className="text-xs mt-2 block">{name}</span>
      </div>
        </Button>
      ))}
    </nav>

    {/* User profile menu */}
    <div className="flex space-x-2 md:space-x-4 items-center gap-7">
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" className="relative h-8 w-8 rounded-full">
            <Avatar className="h-8 w-8 mr-2">
              {user?.profilePicture ? (
                <AvatarImage src={user?.profilePicture} alt={user?.username} />
              ) : (
                <AvatarFallback className="text-foreground">
                  {userPlaceholder}
                </AvatarFallback>
              )}
            </Avatar>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent className="w-64 z-50 bg-[rgb(235,245,250)] dark:bg-[rgb(8,4,20)] text-foreground border border-foreground" align="end">
          <DropdownMenuLabel className="font-normal">
            <div className="flex flex-col space-y-1">
              <div className="flex items-center">
                <Avatar className="h-8 w-8 mr-2">
                  {user?.profilePicture ? (
                    <AvatarImage src={user?.profilePicture} alt={user?.username} />
                  ) : (
                    <AvatarFallback className="text-foreground">
                      {userPlaceholder}
                    </AvatarFallback>
                  )}
                </Avatar>
                <div>
                  <p className="text-sm font-medium leading-none">{user?.username}</p>
                  <p className="text-xs mt-2 text-foreground leading-none">{user?.email}</p>
                </div>
              </div>
            </div>
          </DropdownMenuLabel>
          <DropdownMenuSeparator className="bg-foreground" />
          <DropdownMenuItem
            className="cursor-pointer text-foreground"
            onClick={() => handleNavigation(`/user-profile/${user?._id}`)}
          >
            <Users /> <span className="ml-2">Profile</span>
          </DropdownMenuItem>
          <DropdownMenuItem
            onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
            className="cursor-pointer text-foreground"
          >
            {theme === "light" ? (
              <>
                <Moon className="mr-2" />
                <span>Dark Mode</span>
              </>
            ) : (
              <>
                <Sun className="mr-2" />
                <span>Light Mode</span>
              </>
            )}
          </DropdownMenuItem>
            <DropdownMenuSeparator className="bg-foreground" />
          <DropdownMenuItem
            className="cursor-pointer text-foreground"
            onClick={handleLogout}
          >
            <LogOut className="text-red-500" strokeWidth={3}/> <span className="ml-2 font-bold text-md text-red-500">Logout</span>
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  </div>
</header>
  );
};

export default Header;